import React from 'react';
import {isEmpty} from 'lodash';
import { Chart, Geom, Axis, Tooltip, Label, Coord} from 'bizcharts';

const ChartSimpleBar = props => {
  const {data = [], rotate, padding = [10, 10, 30, 40]} = props;
  return !isEmpty(data) ? (
    <Chart width={props.width} height={props.height} data={data} forceFit padding={rotate ? [10, 15, 30, 120] : padding}>
      <Axis name="name" tickLine={null} />
      <Axis name="days" tickLine={null} grid={
        {
          lineStyle: {
            stroke: '#033154',
            lineDash: [1, 0]
          }
        }
      } />
      { rotate && <Coord transpose />}
      <Tooltip containerTpl='<div class="g2-tooltip"><p class="g2-tooltip-title"></p><table class="g2-tooltip-list"></table></div>'
      itemTpl='<tr class="g2-tooltip-list-item"><td style="color:{color}">延期天数</td><td>{value}</td></tr>'
      offset={50}
      g2-tooltip={{
        position: 'absolute',
        visibility: 'hidden',
        border : '1px solid #efefef',
        backgroundColor: 'white',
        color: '#000',
        opacity: '0.8',
        padding: '5px 15px',
        transition: 'top 200ms,left 200ms'
      }}
      g2-tooltip-list={{
        margin: '10px'
      }}/>
      <Geom type="interval" position="name*days" color={props.color ? props.color : 'type'}>
        <Label content={["name*days", (name, days) => days]}
          textStyle={{
            fill: '#fff', // 文本的颜色
            fontSize: '12', // 文本大小
            fontWeight: 'bold', // 文本粗细
            textBaseline: 'top', // 文本基准线，可取 top middle bottom，默认为middle
          }}
          offset='-10'
        />
      </Geom>
    </Chart>
  ) : null;
}

export default ChartSimpleBar;