import React from 'react';
import {isEmpty} from 'lodash';
import { Chart, Geom, Axis, Tooltip, Label, Coord} from 'bizcharts';

const ChartLine = props => {
  const {data = [], rotate, padding = [10, 10, 30, 40]} = props;
  const cols = {
    value: {
      min: 0
    },
    name: {
      range: [0, 1]
    }
  };
  return !isEmpty(data) ? (
    <Chart width={props.width} height={props.height} scale={cols} data={data} forceFit padding={rotate ? [10, 15, 30, 120] : padding}>
      <Axis name="name" tickLine={null} />
      <Axis name="value" tickLine={null} grid={
        {
          lineStyle: {
            stroke: '#033154',
            lineDash: [1, 0]
          }
        }
      } />
      <Tooltip
        crosshairs={{
          type: "y"
        }}
      />
      <Geom type="line" position="name*value" size={2} />
      <Geom
        type="point"
        position="name*value"
        size={4}
        shape={"circle"}
        style={{
          stroke: "#fff",
          lineWidth: 1
        }}
      />
    </Chart>
  ) : null;
}

export default ChartLine;